.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: var(--theme-color-secondary);
  border-radius: 50%;

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  body.loaded &:not(:hover)::before {
    @include center(scale(1));
  }

  &::before {
    @include center(scale(0));
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    transition: transform 0.5s cubic-bezier(0.4, 1.6, 0.6, 1);
    will-change: transform;
    background-color: $c-light;
    border: 2px solid $c-light;
    z-index: -1;
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {
    color: var(--theme-color-primary);

    &::before {
      @include center(scale(1.125));
    }
  }

  &:focus {
    color: var(--theme-color-primary);
    outline: none;

    &::before {
      border-color: var(--theme-color-primary);
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;

    &::before {
      width: 100%;
      height: 100%;
      background-color: var(--theme-color-primary);
      border-color: var(--theme-color-primary);
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;

    .social-block__link {
      opacity: 1;
      transform: translateY(0);

      &--insta {
        transition-delay: 0.15s;
      }

      &--vk {
        transition-delay: 0.3s;
      }
    }
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;
  opacity: 0;
  transform: translateY(1.5rem);
  transition: all 0.2s;

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: var(--theme-color-secondary);
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: var(--theme-color-secondary);
  }

  &:hover {
    color: var(--theme-color-primary);
  }

  &:focus {
    color: var(--theme-color-primary);
    background-color: $c-light;
    border-color: var(--theme-color-primary);
    outline: none;
  }

  &:active,
  &:active:focus {
    color: var(--theme-color-primary);
    background-color: $c-light;
    border-color: $c-light;
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}
